.App {
  display:flex;
margin: auto;
}

.App-head {
  display: flex;
  background-color: navy;
  color: white;
  justify-content:space-between;
}

.App-title {
  font-size: 30px;
  margin-left: 10px;
}

.App-user {
  font-size: 20px;
  margin-right: 10px;
}


.App-sidemenu {
  width:25%;
  position:absolute;
  /* justify-content: start; */
}

.App-content {
  width:75%;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
